const DEFAULT_NETWORK_ID = parseInt(
    process.env.VUE_APP_DEFAULT_NETWORKID || '1'
)

export const DJTX_ID =
    DEFAULT_NETWORK_ID === 1
        ? (process.env.VUE_APP_DJTXID as string)
        : (process.env.VUE_APP_TEST_DJTXID as string)

export const BAGUETTE_ID =
    DEFAULT_NETWORK_ID === 1
        ? 'F4MyJcUvq3Rxbqgd4Zs8sUpvwLHApyrp4yxJXe2bAV86Vvp38'
        : undefined

interface AssetDict {
    [key: string]: {
        logo: string
    }
}

const dict: AssetDict = {}

dict[DJTX_ID] = {
    logo: 'img/31.png',
}

if (BAGUETTE_ID) {
    dict[BAGUETTE_ID] = {
        logo: 'img/rogue_icon_circle.jpg',
    }
}

export default dict
