<template>
    <div id="network_statistics" class="card">
        <div class="header">
            <h2 class="top_info_heading">Transaction History</h2>
        </div>
        <div class="history_settings">
            <button
                :active="scope === options[0]"
                @click="setScope(options[0])"
            >
                Yr
            </button>
            <button
                :active="scope === options[1]"
                @click="setScope(options[1])"
            >
                Mo
            </button>
            <button
                :active="scope === options[2]"
                @click="setScope(options[2])"
            >
                Wk
            </button>
            <button
                :active="scope === options[3]"
                @click="setScope(options[3])"
            >
                D
            </button>
            <button
                :active="scope === options[4]"
                @click="setScope(options[4])"
            >
                Hr
            </button>
            <button
                :active="scope === options[5]"
                @click="setScope(options[5])"
            >
                Min
            </button>
        </div>
        <div class="canv_cont">
            <canvas ref="canv"></canvas>
        </div>
    </div>
</template>
<script>
import axios from '@/axios'
import Chart from 'chart.js'
import moment from 'moment'

export default {
    data() {
        return {
            context: null,
            options: ['year', 'month', 'week', 'day', 'hour', 'minute'],
            scope: 'day',
            history: null,
            chart: null,
        }
    },
    computed: {
        intervalMs() {
            let res = 0
            switch (this.scope) {
                case 'year':
                    res = 1000 * 60 * 60 * 24 * 365
                    break
                case 'month':
                    res = 1000 * 60 * 60 * 24 * 30
                    break
                case 'week':
                    res = 1000 * 60 * 60 * 24 * 7
                    break
                case 'day':
                    res = 1000 * 60 * 60 * 24
                    break
                case 'hour':
                    res = 1000 * 60 * 60
                    break
                case 'minute':
                    res = 1000 * 60
                    break
            }
            return res
        },
        intervalSize() {
            let res = 'minute'
            switch (this.scope) {
                case 'year':
                    res = 'month'
                    break
                case 'month':
                    // res = `${24*7}h`;
                    res = `day`
                    break
                case 'week':
                    res = 'day'
                    break
                case 'day':
                    res = 'hour'
                    break
                case 'hour':
                    res = '5m'
                    break
                case 'minute':
                    res = '1s'
                    break
            }
            return res
        },
        intervalSizeMs() {
            let res = 0
            switch (this.intervalSize) {
                case 'month':
                    res = 1000 * 60 * 60 * 24 * 30
                    break
                case 'day':
                    res = 1000 * 60 * 60 * 24
                    break
                case 'hour':
                    res = 1000 * 60 * 60
                    break
                case '5m':
                    res = 1000 * 60 * 5
                    break
                case '5s':
                    res = 1000 * 5
                    break
            }
            return res
        },
        intervalFormat() {
            let res = ''
            const scope = this.scope
            switch (this.intervalSize) {
                case 'month':
                    res = 'MMM'
                    break
                case `${24 * 7}h`:
                    res = 'd'
                    break
                case 'day':
                    if (scope === 'month') {
                        res = 'MMM DD'
                    } else {
                        res = 'D dd'
                    }
                    break
                case '2h':
                    res = 'HH:mm'
                    break
                case 'hour':
                    res = 'HH:mm'
                    break
                case '5m':
                    res = 'HH:mm'
                    break
                case '1s':
                    res = 'mm:ss'
                    break
            }
            return res
        },
        startDate() {
            const startMs = Date.now() - this.intervalMs
            return new Date(startMs)
        },
        endDate() {
            return new Date()
        },
        dataX() {
            if (!this.history) return []
            const res = []
            const intervals = this.history.intervals
            intervals.forEach((val, i) => {
                res.push(val)
            })
            return res
        },
        valuesX() {
            const res = []

            for (let i = 0; i < this.dataX.length; i++) {
                const data = this.dataX[i]
                const txCount = data.transactionCount
                res.push(txCount)
            }
            return res
        },
        labelsX() {
            const res = []

            // let date = this.startDate;
            // let len = this.dataX.length;
            // let intervalSizeMs = this.intervalSizeMs;

            const datas = this.dataX
            const len = datas.length

            for (let i = 0; i < len; i++) {
                const data = datas[i]

                const date = new Date(data.startTime)
                const mom = moment(date)

                // if(i%2===0){
                //     res.push('')
                //     continue;
                // }

                const label = mom.format(this.intervalFormat)
                res.push(label)
            }

            return res
        },
    },
    created() {
        this.updateHistory()
    },
    mounted() {
        const cont = this.$refs.canv.getContext('2d')
        this.context = cont

        const valuesX = this.valuesX

        const myLineChart = new Chart(cont, {
            type: 'line',
            // maintainAspectRatio: false,
            data: {
                labels: [
                    'January',
                    'February',
                    'March',
                    'April',
                    'May',
                    'June',
                    'July',
                ],
                datasets: [
                    {
                        label: 'Tx Volume',
                        backgroundColor: '#976cfa',
                        borderColor: '#976cfa',
                        pointBackgroundColor: 'transparent',
                        pointBorderColor: 'transparent',
                        pointHoverBackgroundColor: '#976cfa',
                        pointHoverBorderColor: '#976cfa',
                        data: valuesX,
                        fill: false,
                    },
                ],
            },
            options: {
                maintainAspectRatio: false,
                responsive: true,
                title: {
                    display: false,
                    text: 'Chart.js Line Chart',
                },
                tooltips: {
                    mode: 'index',
                    enabled: true,
                    intersect: false,
                },
                legend: {
                    display: false,
                },
                hover: {
                    mode: 'nearest',
                    intersect: true,
                },
                scales: {
                    x: {
                        display: true,
                        scaleLabel: {
                            display: true,
                            labelString: 'Month',
                        },
                    },

                    y: {
                        display: false,
                        scaleLabel: {
                            display: true,
                            labelString: 'Value',
                        },
                        ticks: {
                            callback: function (value, index, values) {
                                return '$' + value
                            },
                        },
                    },
                    xAxes: [
                        {
                            gridLines: {
                                color: 'rgba(0, 0, 0, 0.03)',
                            },
                            ticks: {
                                autoSkip: true,
                                maxTicksLimit: 3,
                                maxRotation: 0,
                            },
                        },
                    ],
                    yAxes: [
                        {
                            gridLines: {
                                color: 'rgba(0, 0, 0, 0.03)',
                            },
                            ticks: {
                                autoSkip: true,
                                maxTicksLimit: 3,
                                precision: 0,
                                min: 0,
                            },
                        },
                    ],
                },
            },
        })

        this.chart = myLineChart
    },
    methods: {
        setScope(val) {
            this.scope = val
            this.updateHistory()
        },

        updateHistory() {
            const parent = this
            // let scope = this.scope;

            // our selected interval in ms
            const interval = this.intervalMs
            const intervalSize = this.intervalSize
            const endMs = Date.now()
            // let startMs = endMs - interval;

            const startTime = this.startDate.toISOString()
            const endTime = this.endDate.toISOString()

            // let startSec = Math.round(startMs/1000);
            // let endSec = Math.round(endMs/1000);
            // console.log(startTime);
            // console.log(endTime);
            // console.log( new Date().toISOString());
            axios
                .get(
                    `/v2/transactions/aggregates?startTime=${startTime}&endTime=${endTime}&intervalSize=${intervalSize}`
                )
                .then((res) => {
                    const data = res.data
                    console.log(data)
                    parent.history = data
                    parent.draw()
                })
        },

        clearChart() {
            const chart = this.chart
            chart.data.labels = []
            chart.data.datasets.forEach((dataset) => {
                dataset.data = []
            })
            chart.update()
        },
        draw() {
            this.clearChart()
            const parent = this

            const dataX = this.valuesX
            const chart = this.chart

            dataX.forEach((data, index) => {
                // let date =  Date.parse(data.startTime);
                // console.log(date);
                const label = parent.labelsX[index]

                chart.data.labels.push(label)
                chart.data.datasets.forEach((dataset) => {
                    dataset.data.push(data)
                })
            })
            chart.update()
        },
    },
}
</script>
