import api from '@/axios'
import { resolveResponseData } from '@/services/helpers'
import qs from 'qs'

const TRANSACTIONS_API_BASE_URL =
    process.env.VUE_APP_TRANSACTIONS_V2_API_BASE_URL

export interface ITransactionPayload {
    id?: string | null
    params?: ITransactionParams
}

export interface ITransactionParams {
    chainID?: string[]
    assetID?: string
    address?: string
    disableGenesis?: boolean
    sort?: string
    limit?: number
    startTime?: number
    endTime?: number
    offset?: number // to be deprecated
}

/**
    https://explorerapi.djtx.network/v2/transactions
        ?assetID=wjcHr7ng1qPXeJm5Mh3HzQxqz2S9AGo9UVP78jxh5zfirZxY7
        &sort=timestamp-desc
        &limit=25
        &chainID=hgBiUrBJkih9eoPkLtNaJKJQK8Fc2nmR6yuqkh9BmgNY2dJ3Y
        &chainID=11111111111111111111111111111111LpoYY
 */

/**
 *
 * @param params api release
 * @link https://docs.djtx.network/build/tools/ortelius#list-transactions
 */
export function getTransaction(
    id?: string | null,
    params?: ITransactionParams
) {
    return api
        .get(
            id
                ? `${TRANSACTIONS_API_BASE_URL}/${id}`
                : TRANSACTIONS_API_BASE_URL,
            {
                params,
                paramsSerializer: (params) =>
                    qs.stringify(params, { arrayFormat: 'repeat' }),
            }
        )
        .then(resolveResponseData)
}
