

















import 'reflect-metadata'
import { Vue, Component, Prop } from 'vue-property-decorator'
import { stringToBig } from '@/helper'
import { DJTX_ID } from '@/known_assets'

@Component({})
export default class AddressResult extends Vue {
    @Prop() item!: any | null

    select() {
        // TODO: support for multiple chains. add a chain param
        const url = `/address/X-${this.item.address}`
        this.$router.push(url)
        this.$emit('select')
    }

    get djtxBalance() {
        return stringToBig(
            this.item.assets[DJTX_ID].balance,
            this.djtxDenomination
        )
    }

    get djtxDenomination() {
        return this.$store.state.assets[DJTX_ID].denomination
    }

    get djtxSymbol() {
        return this.$store.state.assets[DJTX_ID].symbol
    }

    get numTx() {
        return this.item.transactionCount
    }

    get chainNow() {
        return this.$store.state.chainId
    }

    get prefixedAddress() {
        return `${this.chainNow}-${this.item.address}`
    }
}
